.reset_history {
    font-size: 16px;
    background-color: transparent;
    border: 2px solid cyan;
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
}

.reset_history:hover {
    background-color: rgb(224, 224, 224);
    border-color: rgb(3, 204, 204);
    transition: 0.3s;
}